import './App.css';
import React from 'react';
import { BrowserRouter as Router , Route , Routes } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Authpage from './Pages/Authpage';
import UserInfo from './Pages/UserInfo';
import ProfilePic from './Pages/ProfilePic';
import SocialMedia from './Pages/SocialMedia';
import Home from './Pages/Home';
import Search from './Pages/Search';
import Events from './Pages/Events';
import Profile from './Pages/Profile';
import Notfication from './Pages/Notfication';
import UserPage from './Pages/UserPage';
import CreateProfileFolioPage from './Pages/CreateProfileFolioPage';
import SkillPage from './Pages/SkillPage';


function App() {
  return (
    <Router>
      <div className='App'>
        <ToastContainer position='top-center'/>
        <Routes>
          <Route exact path='/' Component={Authpage}></Route>
          <Route  path='/userinfo' Component={UserInfo}></Route>
          <Route  path='/profile-picture-upload' Component={ProfilePic}></Route>
          <Route  path='/socialmedia' Component={SocialMedia}></Route>
          <Route  path='/home' Component={Home}></Route>
          <Route  path='/search' Component={Search}></Route>
          <Route  path='/events' Component={Events}></Route>
          <Route  path='/profile' Component={Profile}></Route>
          <Route  path='/profile/:uid' Component={UserPage}></Route>
          <Route  path='/search/profile/:uid' Component={UserPage}></Route>
          <Route  path='/notification' Component={Notfication}></Route>
          <Route  path='/createprofilefolio/:userUID' Component={CreateProfileFolioPage}></Route>
          <Route  path='/skillset/:uid' Component={SkillPage}></Route>
          
        </Routes>
      </div>
    </Router>
  );
}

export default App;
