import React,{useEffect, useState} from 'react'
import Header from '../Components/Header'
import NavBar from '../Components/NavBar'
import Post from '../Components/Post'
import '../ComponentsCSS/post.css'
import PostCreateButton from '../Components/PostCreateButton'
import { db } from '../firebase'
import { ref , set , push, update, get, orderByChild  } from 'firebase/database';
import { toast } from 'react-toastify'
import { redirect } from 'react-router-dom'



const Home = () => {
  const [posts, setPosts] = useState({})
  const [postKey, setPostKey] = useState('')
  const [blogContent , setBlogContent ] = useState('');
  const uid = localStorage.getItem('uid')


  useEffect(() => {
    const postRef = ref(db, "/posts");
  
    get(postRef)
      .then((snapshot) => {
        if (snapshot.exists()) {
          const posts = snapshot.val();
          const postsArray = Object.values(posts);
          const reversedPosts = postsArray.reverse();

          setPosts(reversedPosts);
          console.log(posts)
        }
      });
  }, []);

  const handleBlog = (e) => {
    setBlogContent(e.target.value)
  }


  const handlePostDB = () => {
    if(blogContent===""){
      toast.error("Blog cannot be empty")
    }else{
      const postRef = ref(db, `/posts/`)

      const newPostRef = push(postRef, {
         content : blogContent,
         user: uid,
         timestamp: Date.now()
       })
       const newPostKey = newPostRef.key
       setPostKey(newPostKey)
       window.location.reload();
       toast.success("Blog posted Successfully")
       
    }


 }



  return (
    <div className='home-page'>
      <Header/>
      <div className="body-container">
        <NavBar/>
        <div className="post-container">
          <PostCreateButton handlePostDB={handlePostDB} handleBlog={handleBlog} blogContent={blogContent}/>
          {Object.keys(posts).map((postID) => (
            <Post key={postID} postKey={postID} post={posts[postID]} id={posts[postID].user} />
          ))}         
        </div>
      </div>
    </div>
  )
}

export default Home