import React, { useEffect, useState } from 'react'
import { db } from '../firebase';
import { ref , set , push, update , get } from 'firebase/database';import { useNavigate } from 'react-router-dom'

const Post = ({postKey, post, id}) => {
  const [user, setUser] = useState()
  const [likePost , setLikePost] = useState(false)
  const [commentPost , setCommentPost ] = useState("")

useEffect(()=>{
  const userInfo = ref(db, `users/${id}`)
  get(userInfo)
  .then((snapshot)=>{
   if(snapshot.exists()){
     setUser(snapshot.val())
     console.log(user)
   }
  })
},[])



  const uid = localStorage.getItem('uid')
  const navigate = useNavigate()

  const handleLike = () => {
    
    const likeRef = ref(db, `/posts/${uid}/${postKey}/likes`)
    if(likePost){
      update(likeRef, {
        [uid]: null, 
      }).then(() => {
        setLikePost(false);
        
      alert('You disliked the Post')
      });
    }else{
      set(likeRef, {
        [uid]: true,
      }).then(() => {
        setLikePost(true);
      });
      alert('You Liked the Post')
    }

  }



  return (
    <div className='post-box'>
        <div className="post-user">
        <img src={user?.profiles.profileURL} alt="" className='post-user-profile' />
            <div className="user-details-post">
              <h1 onClick={()=>{alert(user.profilename)}}>{user?.profilename}</h1>
              <p>@{user?.username}</p>
            </div>
            <button onClick={()=>{ window.location.href = `profile/${user.uid}` }}>View</button>
        </div>
        <div className="post-content">
                <p>{post.content}</p>
        </div>
        <div className="post-status">
          <div className="post-function">
            <img src="https://cdn-icons-png.flaticon.com/128/1077/1077035.png" alt="" onClick={handleLike}/> 
            <p>20</p>
          </div>
          <div className="post-function">
            <img src="https://cdn-icons-png.flaticon.com/128/2462/2462719.png" alt="" /> 
            <p>20</p>
          </div>
          <div className="post-function">
            <img src="https://cdn-icons-png.flaticon.com/128/2958/2958783.png" alt="" /> 
          </div>
          <div className="post-function">
            <img src="https://cdn-icons-png.flaticon.com/128/9511/9511721.png" alt="" /> 
          </div>
        </div>
    </div>
  )
}

export default Post