import React from 'react'
import "../ComponentsCSS/navbar.css"
import { useNavigate } from 'react-router-dom'
import { auth } from '../firebase';

const NavBar = ({user}) => {
    const navigate = useNavigate();

    const handleSignOut = async () => {
        try {
          await auth.signOut();
          localStorage.clear();
          navigate('/'); 
        } catch (error) {
          console.error('Error signing out:', error);
        }
      };

  return (
    <div className='navbar-container'>
        <div className="nav-button" onClick={()=> {navigate('/home')}}>
            <img src="https://cdn-icons-png.flaticon.com/512/25/25694.png" alt="" />
            <h2>Home</h2>
        </div>
        <div className="nav-button" onClick={()=> {navigate('/search')}}>
            <img src="https://cdn-icons-png.flaticon.com/512/61/61088.png" alt="" />
            <h2>Search</h2>
        </div>
        <div className="nav-button" onClick={()=> {navigate('/events')}}>
            <img src="https://www.citypng.com/public/uploads/small/11640487321kzngzwhl4xjx4fmyj7fpzntijpwi9ubqobum84ysu6zolaibglvy9o07wqaordtxe6cdarnhrofibhfxrvzwwmfkedcylp7x0sn2.png" alt="" />
            <h2>Events</h2>
        </div>
        <div className="nav-button" onClick={()=> {navigate('/profile')}}>
            <img src="https://icon-library.com/images/profile-png-icon/profile-png-icon-2.jpg" alt="" />
            <h2>Profile</h2>
        </div>
        <div className="nav-button" onClick={()=> {navigate('/notification')}}>
            <img src="https://cdn-icons-png.flaticon.com/512/565/565422.png" alt="" />
            <h2>Notification</h2>
        </div>
        <div className="nav-button signout" onClick={handleSignOut} >
            <img src="https://cdn-icons-png.flaticon.com/512/25/25376.png" alt="" />
            <h2>Signout</h2>
        </div>
    </div>
  )
}

export default NavBar